import * as React from "react"
import Header from "../componets/header/Header"
import Footer from "../componets/footer/Footer"
import { getContent } from "../content/content"
import { useLanguage } from "../context/LanguageContext"

const PrivacyPolicyPage = () => {
  const { lang } = useLanguage();
  const content = getContent(lang);
  const privacyContent = content.pages.privacyPolicy?.section || {
    title: lang === "en" ? "Privacy Policy" : "Chính sách Bảo mật",
    lastUpdated: lang === "en" ? "Last updated:" : "Cập nhật lần cuối:",
    sections: [
      {
        title: lang === "en" ? "Information We Collect" : "Thông tin chúng tôi thu thập",
        content: lang === "en" ? "We collect information you provide directly to us, such as when you contact us or use our services." : "Chúng tôi thu thập thông tin bạn cung cấp trực tiếp cho chúng tôi, chẳng hạn như khi bạn liên hệ với chúng tôi hoặc sử dụng dịch vụ của chúng tôi."
      },
      {
        title: lang === "en" ? "How We Use Your Information" : "Cách chúng tôi sử dụng thông tin của bạn",
        content: lang === "en" ? "We use the information we collect to provide, maintain, and improve our services." : "Chúng tôi sử dụng thông tin chúng tôi thu thập để cung cấp, duy trì và cải thiện dịch vụ của chúng tôi."
      },
      {
        title: lang === "en" ? "Information Sharing" : "Chia sẻ thông tin",
        content: lang === "en" ? "We do not sell, trade, or otherwise transfer your personal information to third parties without your consent." : "Chúng tôi không bán, trao đổi hoặc chuyển giao thông tin cá nhân của bạn cho bên thứ ba mà không có sự đồng ý của bạn."
      },
      {
        title: lang === "en" ? "Contact Us" : "Liên hệ với chúng tôi",
        content: lang === "en" ? "If you have any questions about this Privacy Policy, please contact us." : "Nếu bạn có bất kỳ câu hỏi nào về Chính sách Bảo mật này, vui lòng liên hệ với chúng tôi."
      }
    ]
  };

  return (
    <main style={{ background: '#1A202C', minHeight: '100vh', color: 'white' }}>
      <Header content={content?.pages?.globals?.section?.headerContent} />
      <div style={{ 
        padding: '8rem 2rem 4rem', 
        maxWidth: '800px', 
        margin: '0 auto',
        lineHeight: '1.6'
      }}>
        <h1 style={{ 
          fontSize: '2.5rem', 
          marginBottom: '1rem', 
          color: '#d1a12a',
          fontWeight: '700'
        }}>{privacyContent.title}</h1>
        <p style={{ marginBottom: '3rem', color: '#a0aec0' }}>
          <strong>{privacyContent.lastUpdated}</strong> {new Date().toLocaleDateString()}
        </p>
        
        {privacyContent.sections.map((section: any, index: number) => (
          <div key={index}>
            <h2 style={{ 
              fontSize: '1.5rem', 
              marginTop: '2.5rem', 
              marginBottom: '1rem',
              color: '#00bcd4'
            }}>{section.title}</h2>
            <p style={{ marginBottom: '1.5rem' }}>{section.content}</p>
          </div>
        ))}
      </div>
      <Footer content={content?.pages?.globals?.section?.footerContent} />
    </main>
  )
}

export default PrivacyPolicyPage